*{
  box-sizing: border-box;
}
html,body{
  margin: 0;
  padding: 0;
}
ul{
  list-style: none;
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;

}
button,input{
  background: transparent;
  border: none;
  padding: 0;
  margin: 0;
  &:active,&:focus{
    outline: 1px rgba(255,158,180,0.5);
  }
}