.btn{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
  text-align: center;
  transition: all .3s ease 0s;
  min-height: 48px;

  &._fw{
    width: 100%;
  }
  &_white {
    color: $orange-color;
    background: $white-color;

    @media (any-hover: hover) {
      &:hover{
        color: $white-color;
        background: $orange-color;
      }
    }
  }
}