.footer {
  &-title {
    font-weight: bold;
    @include adaptive-value("font-size", 24, 20, 1);
    line-height: 29/24 *100%;
    color: #000;

    @media (min-width: $md3+px) {
      margin-bottom: 16px/24px *1em;
    }
  }

  // .footer__container
  &__container {}

  // .footer__body
  &__body {
    border-top: 1px solid #d8d8d8;
    display: grid;
    grid-template-columns: 250px 2fr 290px;
    @include adaptive-value("padding-top", 80, 30, 1);
    @include adaptive-value("padding-bottom", 60, 30, 1);
    @include adaptive-value("column-gap", 80, 1, 1);

    @media (max-width: $md1+px) {
      grid-template-columns: 1fr 290px;
      grid-template-rows: auto auto;
    }

    @media (max-width: $md2+px) {
      display: block;
    }
  }

  // .footer__main
  &__main {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @media (max-width: $md1+px) {
      grid-column: 1/-1;
      @include adaptive-value("margin-bottom", 40, 30, 1);
    }
  }

  // .footer__logo
  &__logo {}

  // .footer__text
  &__text {
    color: $dark-gray-color;
    margin-bottom: 1em;
    line-height: 150%;
  }

  // .footer__menu
  &__menu {
    @media (max-width: $md1+px) {
      grid-column: 1/2;
      grid-row: 2/3;
    }

    @media (max-width: $md2+px) {
      @include adaptive-value("margin-bottom", 40, 30, 1);
    }
  }

  // .footer__subscribe
  &__subscribe {
    @media (max-width: $md1+px) {
      grid-column: 2/3;
      grid-row: 2/3;
    }
  }
}

.contacts-footer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  @include adaptive-value("gap", 16, 10, 1);

  &__item {
    color: $dark-gray-color;
    display: inline-flex;
    align-items: center;
    font-family: $fontFamily !important;
    line-height: 150%;
    transition: all .3s ease 0s;

    &::before {
      @extend %ic;
      margin-right: 8px;
    }

    @media (any-hover: hover) {
      &:hover {
        color: $orange-color;
      }
    }
  }
}

.menu-footer {
  @media (min-width: $md3+px) {
    display: flex;
  }

  // .menu-footer__colunm
  &__colunm {
    @media (min-width: $md3+px) {
      // display: flex;
      // flex-direction: column;
      // align-items: flex-start;
      flex: 0 1 33.333%;
    }

    @media (max-width: $md3+px) {
      @include adaptive-value("margin-bottom", 30, 15, 1);
    }
  }

  // .menu-footer__title
  &__title {
    @media (min-width: $md3+px) {
      margin-bottom: 16px/24px*1em;
    }

    @media (max-width: $md3+px) {
      cursor: pointer;
    }

    .init & {
      display: flex;
      flex-direction: row-reverse;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      @extend .icon-arrow-down;

      &:before {
        @extend %ic;
        font-size: 12px;
        transition: all .3s ease 0s;
      }
    }

    &.active {
      &:before {
        transform: rotate(-180deg);
      }
    }
  }

  // .menu-footer__list
  &__list {
    // display: flex;
    flex-direction: column;
    gap: 16px;

    li {
      &:not(:last-child) {
        margin: 0 0 16px 0;
      }
    }

    @media (max-width: $md3+px) {
      padding-top: 15px;
    }
  }

  // .menu-footer__link
  &__link {
    color: $dark-gray-color;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    transition: all .3s ease 0s;


    @media (any-hover: hover) {
      &:hover {
        color: $orange-color;
      }
    }
  }
}

.subscribe {

  // .subscribe__title
  &__title {
    margin-bottom: 16px/24px*1em;
  }

  // .subscribe__form
  &__form {
    display: flex;
    position: relative;
  }

  // .subscribe__btn
  &__btn {
    display: flex;
    flex: 0 0 45px;
    height: 45px;
    justify-content: center;
    align-items: center;
    color: $white-color;
    background: $orange-color;
    font-size: 24px;
    margin-left: 4px;
    transition: all .3s ease 0s;
    @media (any-hover: hover) {
      &:hover{
        background: $orange-hover-color;
      }
    }
  }

  .input {
    width: 100%;
    padding: 0 12px;
    background-color: #F4F5F7;
    font-size: 16px;
    line-height: 24px;
  }
}