.modal {
  &-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    transition: all .3s ease-in;
  }

  &-window {
    width: 600px;
    margin: 0 auto;
    border-radius: 5px;
    background: #fff;
    display: flex;
    flex-direction: column;
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease-in;
    transform: translateY(-200px);
  }

  &-header {
    display: flex;
    justify-content: space-between;
    padding: 5px 10px;
    border-bottom: 1px solid #eee;
  }

  &-title {
    font-size: 1.5rem;
  }

  &-close {
    font-size: 2rem;
    background: transparent;
    width: 24px;
    height: 24px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
  }

  &-body {
    padding: 1rem;
    flex: 1;
  }

  &-footer {
    display: flex;
    justify-content: flex-end;
    gap: 15px;
    padding: 5px 10px;
    border-top: 1px solid #eee;
  }

  &.open {

    .modal-overlay,
    .modal-window {
      opacity: 1;
      visibility: visible;
      z-index: 1000;
    }

    .modal-overlay {
      background: rgba(0, 0, 0, .5);
    }

    .modal-window {
      transform: translateY(100px);
    }
  }
}