//  SLIDER
.main-slider {
  background: #FCF8F3;

  // .main-slider__container
  &__container {
    position: relative;

    @media (min-width: $md2+px) {
      &:before {
        content: "";
        background: #F9F1E7;
        position: absolute;
        top: 0;
        width: 100vw;
        right: 376px;
        height: 100%;
      }
    }
  }

  // .main-slider__body
  &__body {
    padding-top: 140px;
    position: relative;
    z-index: 2;

    @media (max-width: $md2+px) {
      padding-top: 80px;
    }

    @media (max-width: $md3+px) {
      padding-top: 50px;
    }

    @media (min-width: $md2+px) {
      display: flex;
      min-width: 0;
    }
  }

  // .main-slider__content
  &__content {
    flex: 0 0 494/1240 * 100%;
  }

  // .main-slider__slider
  &__slider {
    flex: 1 1 auto;
    min-width: 0;
  }
}

.content-main {
  position: relative;
  z-index: 5;
  padding-left: 15px;
  padding-right: 15px;
  align-self: flex-start;

  @include adaptive-value("padding-top", 60, 15, 1);
  @include adaptive-value("padding-bottom", 60, 15, 1);

  &:not(:last-child) {
    @include adaptive-value("margin-bottom", 60, 15, 1);
  }

  background: rgba(255, 255, 255, 0.72);

  @media (max-width: $md2+px) {
    margin: 0 -15px;
  }

  @media (min-width: $md2+px) {
    backdrop-filter: blur(31px);
    @include adaptive-value("padding", 60, 1, 1);
  }

  // .content-main__title
  &__title {
    font-weight: 700;
    @include adaptive-value("font-size", 56, 24, 1);
    line-height: 120%;
    margin-top: 0;
    margin-bottom: 0;

    &:not(:last-child) {
      margin: 0 0 16px/56px*1em 0;
    }
  }

  // .content-main__text
  &__text {
    @include adaptive-value("font-size", 20, 16, 1);
    color: $gray-color;
    font-weight: 500;
    line-height: 150%;

    &:not(:last-child) {
      margin: 0 0 48px/20px*1em 0;
    }
  }

  // .content-main__btn
  &__btn {
    align-self: center;
    font-weight: 600;
    color: $white-color;
    @include adaptive-value("min-height", 78, 45, 1);
    @include adaptive-value("font-size", 20, 16, 1);
    background: $orange-color;
    width: 100%;

    @media (any-hover: hover) {
      &:hover {
        background: #e28349;
      }
    }
  }
}

.slider {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  @media (max-width: $md2+px) {
    align-items: stretch;
    flex-direction: column-reverse;

  }

  // .slider__controls
  &__controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    align-self: stretch;

    @media (min-width: $md2+px) {
      margin-left: 32px;
    }

    @media (max-width: $md2+px) {
      margin-top: 10px;
    }
  }

  // .slider__dots
  &__dots {
    flex: 1 1 auto;

    @media (max-width: $md2+px) {
      justify-content: center;
    }
  }

  // .slider__arrows
  &__arrows {
    display: grid;
    grid-template-columns: repeat(2, 48px);
    column-gap: 8px;
  }

  // .slider__arrow
  &__arrow {
    pointer-events: auto;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: rgba($orange-color, .3);
    color: $orange-color;
    transition: all .3s ease 0s;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 12px;

    @media (any-hover: hover) {
      &:hover {
        background: rgba($orange-color, 1);
        color: $white-color;
      }
    }

    &_white {
      background: $white-color;
      box-shadow: 0px 4px 14px 1px rgba(0, 0, 0, 0.16);
    }
  }

  // .slider__arrow_prev
  &__arrow_prev {
    &:before {
      transform: rotate(90deg) translateY(1px);
    }
  }

  // .slider__arrow_next
  &__arrow_next {
    &:before {
      transform: rotate(-90deg) translateY(1px);
    }
  }

  // .slider__body
  &__body {
    overflow: visible;
    min-width: 0;

    @media (min-width: $md2+px) {
      @include adaptive-value("width", 934, 290, 1);
    }

    @media (max-width: $md2+px) {
      margin: 0 -15px;
    }
  }

  // .slider__image
  &__image {
    padding-bottom: 59.16%;
  }
}

.swiper {
  &-wrapper {
    display: flex;
    width: 100%;
    z-index: 1;
  }

  &-slide {
    flex-shrink: 0;
    width: 100%;
    position: relative;
  }

  &-pagination {
    &-bullets {
      display: flex;
      flex-wrap: wrap;
      // justify-content: space-between;
      align-items: center;
      gap: 12px;
    }

    &-bullet {
      flex: 0 0 27px;
      height: 27px;
      cursor: pointer;
      position: relative;
      justify-content: center;
      align-items: center;
      display: flex;

      &:after {
        content: "";
        width: 11px;
        height: 11px;
        background: #d8d8d8;
        transition: all .3s ease 0s;
        border-radius: 50%;
      }

      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        border: 1px solid $orange-color;
        transition: all .3s ease 0s;
        transform: scale(0);
      }

      &-active {
        &:after {
          background: $orange-color;
        }

        &:before {
          transform: scale(1);
        }
      }
    }
  }
}

.slide {
  position: relative;

  // .slide__content
  &__content {
    min-width: 295px;
    max-height: 300px;
    overflow: auto;
    color: inherit;
    display: flex;
    flex-direction: column;
    @include adaptive-value("padding", 24, 15, 1);

    @media (min-width: $md3+px) {
      position: absolute;
      min-width: 350px;
      @include adaptive-value("right", 40, 0, 1);
      @include adaptive-value("bottom", 40, 0, 1);
      background: rgba($white-color, .5);
      backdrop-filter: blur(31px);
    }
  }

  // .slide__title
  &__title {
    @include adaptive-value("font-size", 28, 20, 1);
    font-weight: 600;
    line-height: 120%;

    &:not(:last-child) {
      margin-bottom: 4px/24px*1em;
    }
  }

  // .slide__text
  &__text {
    line-height: 120%;
    color: $gray-color;

    &:not(:last-child) {
      margin-bottom: 8px/16px*1em;
    }
  }

  // .slide__price
  &__price {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    line-height: 150%;
    font-family: $fontFamily !important;
    @include adaptive-value("font-size", 20, 16, 1);
    font-weight: 600;

    &:before {
      @extend %ic;
      margin-left: 10px;
      font-size: 14px;
    }
  }
}

// ============ advantages ===========================

.advantages {
  @include adaptive-value("padding-top", 88, 30, 1);
  @include adaptive-value("padding-bottom", 88, 30, 1);

  // .advantages__container
  &__container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }

  // .advantages__item
  &__item {
    display: grid;
    grid-template-columns: 85px 1fr;
    grid-template-rows: auto auto;
    align-items: center;
    padding: 15px 0;

    @media (max-width: $md2+px) {
      min-width: 280px;
    }
  }

  // .advantages__icon
  &__icon {
    grid-column: 1/2;
    grid-row: 1/3;
    display: flex;
    justify-content: center;
    align-content: center;
  }

  // .advantages__title
  &__title {
    grid-column: 2/3;
    grid-row: 1/2;
    margin: 0;
    margin-bottom: 2px/18px*1em;
    font-weight: 600;
    font-size: 18px;
    line-height: 150%;
  }

  // .advantages__text
  &__text {
    grid-column: 2/3;
    grid-row: 2/3;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: $gray-color;
  }
}

// =========== products =============

.products {
  @include adaptive-value("padding-bottom", 44, 30, 1);

  // .products__container
  &__container {}

  // .products__items
  &__items {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(285px, 1fr));
    gap: 32px;
  }

  // .products__item
  &__item {}

  // .products__footer
  &__footer {
    padding-top: 32px;
    text-align: center;
  }

  // .products__more
  &__more {
    min-width: 285px;
    border: 1px solid $orange-color;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
  }
}

.title {
  font-weight: bold;
  @include adaptive-value("font-size", 40, 24, 1);
  line-height: 120%;
  text-align: center;
  margin-bottom: 32px/40px*1em;
}

.item-product {
  background: #f4f5f7;
  display: flex;
  flex-direction: column;
  position: relative;

  @media (any-hover: hover) {
    &:hover {
      .item-product__actions {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  // .item-product__labels
  &__labels {
    position: absolute;
    z-index: 2;
    display: flex;
    flex-direction: column;
    @include adaptive-value("right", 24, 10, 1);
    @include adaptive-value("top", 24, 10, 1);
  }

  // .item-product__label
  &__label {
    flex: 0 0 48px;
    width: 48px;
    border-radius: 50%;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $white-color;

    &:not(:last-child) {
      margin: 0 0 10px 0;
    }

    // .item-product__label_sale
    &_sale {
      background: #E97171;
    }

    // .item-product__label_new
    &_new {
      background: #2EC1AC;
    }
  }



  // .item-product__image
  &__image {
    padding-bottom: 301px/285px*100%;
  }

  // .item-product__body
  &__body {
    flex: 1 1 auto;
    margin: 16px 16px 30px 16px;
    display: flex;
    flex-direction: column;
  }

  // .item-product__content
  &__content {
    flex: 1 1 auto;
  }

  // .item-product__title
  &__title {
    font-weight: 600;
    @include adaptive-value("font-size", 24, 20, 1);
    line-height: 120%;

    &:not(:last-child) {
      margin: 0 0 8px/24px*1em 0;
    }
  }

  // .item-product__text
  &__text {
    color: $gray-color;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    margin: 0 0 8px/24px*1em 0;
  }

  // .item-product__prices
  &__prices {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    line-height: 150%;
  }

  // .item-product__price
  &__price {
    font-weight: 600;
    @include adaptive-value("font-size", 20, 16, 1);

    // .item-product__price_old
    &_old {
      font-weight: normal;
      @include adaptive-value("font-size", 16, 14, 1);
      text-decoration-line: line-through;
      color: #B0B0B0;
      margin-left: 16px;
    }
  }

  // .item-product__actions
  &__actions {
    @media (any-hover: none) {
      padding-top: 15px;
    }

    @media (any-hover: hover) {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: rgba(58, 58, 58, .75);
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 3;
      transition: all .3s ease 0s;
    }
  }
}

.actions-product {

  // .actions-product__body
  &__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  // .actions-product__button
  &__button {
    flex: 1 1 100%;

    &:not(:last-child) {
      margin: 0 0 24px 0;
    }

    &.hold {
      opacity: 0.2;
      cursor: default;
    }
  }

  // .actions-product__link
  &__link {
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: inherit;
    font-family: $fontFamily !important;
    transition: all .3s ease 0s;

    &:before {
      font-size: 16px;
      @extend %ic;
      margin-right: 10px;
    }

    @media (any-hover: hover) {
      color: $white-color;

      &:hover {
        color: $orange-color;
      }
    }
  }
}

// =========== rooms =============
.rooms {
  background: #FCF8F3;
  @include adaptive-value("padding-top", 44, 30, 1);
  @include adaptive-value("padding-bottom", 44, 30, 1);

  // .rooms__container
  &__container {
    @media (min-width: $md2+px) {
      display: flex;
      align-items: center;
    }
  }

  // .rooms__body
  &__body {
    flex: 0 0 465/1240*100%;
    position: relative;
    z-index: 5;

    @media (max-width: $md2+px) {
      margin-bottom: 30px;
    }
  }

  // .rooms__title
  &__title {
    text-align: left;

    &:not(:last-child) {
      margin: 0 0 8px/40px*1em 0;
    }
  }

  // .rooms__text
  &__text {
    font-weight: 500;
    line-height: 150%;
    max-width: 370px;
    color: $dark-gray-color;

    &:not(:last-child) {
      margin: 0 0 24px 0;
    }
  }

  // .rooms__button
  &__button {
    min-width: 180px;
    background: $orange-color;
    color: $white-color;

    @media (any-hover: hover) {
      &:hover {
        background: $orange-hover-color;
      }
    }
  }

  // .rooms__slider
  &__slider {
    min-width: 0;
    flex: 1 1 auto;
    align-self: flex-start;

    @media (max-width: $md4+px) {
      margin: 0 -15px;
    }
  }
}

.slider-rooms {
  position: relative;

  // .slider-rooms__body
  &__body {
    overflow: visible;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      height: 100%;
      width: 100vw;
      left: 0;
      background: #FCF8F3;
      z-index: 2;
      transform: translateX(-100%);
    }
  }

  // .slider-rooms__slide
  &__slide {
    width: 372px;
    overflow: hidden;
    @include adaptive-value("min-height", 585, 400, 1);

    &.swiper-slide-active {
      .slider-rooms__image {
        height: 100%;
      }
    }

    @media (max-width: $md4+px) {
      width: 100%;
    }
  }

  // .slider-rooms__image
  &__image {
    height: 100%;
    width: 100%;
    transition: all .3s ease 0s;

    @media (min-width: $md3+px) {
      height: 83%;
    }
  }

  // .slider-rooms__content
  &__content {
    position: absolute;
    color: inherit;
    font-family: $fontFamily !important;
    max-width: 240px;
    @include adaptive-value("padding", 32, 15, 1);
    @include adaptive-value("bottom", 24, 15, 1);
    @include adaptive-value("left", 24, 15, 1);
    background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(8px);

    &:before {
      position: absolute;
      left: 100%;
      bottom: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 48px;
      width: 48px;
      background: $orange-color;
      color: $white-color;
      @extend %ic;
    }
  }

  // .slider-rooms__label
  &__label {}

  // .slider-rooms__title
  &__title {
    font-weight: 600;
    @include adaptive-value("font-size", 28, 24, 1);
    line-height: 120%;
  }

  // .slider-rooms__dots
  &__dots {
    @media (min-width: $md3+px) {
      position: absolute;
      bottom: 30px;
      left: 396px;
      width: 100%;
    }

    @media (max-width: $md3+px) {
      justify-content: center;
      padding-top: 15px;
    }
  }

  // .slider-rooms__arrows
  &__arrows {
    @extend .slider__arrows;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 5;
    width: calc(100% - 48px);
    left: 24px;
    justify-content: space-between;
    pointer-events: none;

    @media (min-width: $md1+px) {
      width: calc(100% + 48px);
      left: -24px;
    }
  }
}

.slider-label {
  display: flex;
  flex-direction: row;
  gap: 8px;
  align-items: center;
  color: $dark-gray-color;

  &:not(:last-child) {
    margin: 0 0 8px 0;
  }

  // .slider-label__number
  &__number {}

  // .slider-label__line
  &__line {
    height: 1px;
    width: 20px;
    background: $dark-gray-color;
  }

  // .slider-label__text
  &__text {}
}

// =========== tips =============

.tips {

  // .tips__container
  &__container {}

  // .tips__title
  &__title {}

  // .tips__slider
  &__slider {
    position: relative;
    @include adaptive-value("margin-bottom", 44, 30, 1);

    // .tips__slider__slide
    &__slide {
      display: flex;
      flex-direction: column;
      transition: all .3s ease 0s;

      @media (any-hover: hover) {
        &:hover {
          box-shadow: 0 0 32px rgba(94, 94, 94, .2);
        }
      }
    }

    // .tips__slider__image
    &__image {
      padding-bottom: 251/391*100%;
    }

    // .tips__slider__content
    &__content {
      display: flex;
      flex-direction: column;
      gap: 8px;
      padding: 15px;
    }

    // .tips__slider__title
    &__title {
      color: inherit;
      font-weight: 600;
      @include adaptive-value("font-size", 24, 18, 1);
      line-height: 120%;
      transition: all .3s ease 0s;

      @media (any-hover: hover) {
        &:hover {
          color: $orange-color;
        }
      }
    }

    // .tips__slider__date
    &__date {
      color: $gray-color;
      line-height: 150%;
      font-size: 14px;
    }

    // .tips__slider__dots
    &__dots {
      justify-content: center;
      width: 100%;
    }

    // .tips__slider__arrows
    &__arrows {
      @extend .slider__arrows;
      position: absolute;
      pointer-events: none;
      top: 50%;
      transform: translateY(-50%);
      width: calc(100% - 48px);
      left: 24px;
      justify-content: space-between;
      display: flex;

      @media (min-width: $md1+px) {
        width: calc(100% + 48px);
        left: -24px;
      }

      @media (max-width: $md4+px) {
        display: none;
      }
    }

    // .tips__slider__body
    &__body {
      overflow: hidden;
      @include adaptive-value("margin-right", -30, -15, 1);
      @include adaptive-value("margin-left", -30, -15, 1);
      @include adaptive-value("padding", 30, 15, 1);
    }
  }
}

// =========== share =============
.share {

  // .share__container
  &__container {}

  // .share__text
  &__text {
    text-align: center;
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 8px;
  }

  // .share__title
  &__title {
    margin: 0;
  }

  // .share__body
  &__body {
    position: relative;

    @media (any-hover: none) {
      overflow: auto;
      -webkit-overflow-scrolling: touch;
    }
  }

  // .share__items
  &__items {
    display: grid;
    grid-template-columns: auto;
    grid-auto-flow: column;
    align-items: center;
    gap: 16px;

    @media (any-hover: hover) {
      justify-content: center;
    }
  }

  // .share__column
  &__column {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 0 -8px;
    &:first-child{
      justify-content: flex-end;
    }
  }

  // .share__row
  &__row {
    display: inline-grid;
    padding: 0 8px;
    gap: 16px;
    grid-template-columns: auto;
    grid-auto-flow: column;
    &:not(:last-child){
      margin: 0 0 16px 0;
    }
  }
}


.row {

  // .row-left-top
  &-left-top {
    justify-content: end;
    align-items: end;
  }

  // .row-left-bottom
  &-left-bottom {
    justify-content: end;
    align-items: start;
  }

  // .row-center
  &-center {
    display: block;
  }

  // .row-right-top
  &-right-top {
    align-items: end;
  }

  // .row-right-bottom
  &-right-bottom {
    align-items: start;
  }
}

.row_item {}