.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  min-height: 40px;
  @media (max-width: $md3+px) {
    min-height: 5px;
  }
  &.scroll{
    .header__wrapper{
      background: rgba(255,255,255,.75);
      backdrop-filter: blur(10px);
    }
    .header__body {
      min-height: 50px;
      @media (min-width: $md3+px) {
        min-height: 70px;
      }
    }
}
  &__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 50;
    transition: all .3s ease 0s;
  }

  &__body {
    transition: all .3s ease 0s;
    display: flex;
    align-items: center;
    padding: 5px 0;
    min-height: 125px;
    position: relative;

    @media (max-width: $md2+px) {
      min-height: 80px;
    }

    @media (max-width: $md3+px) {
      min-height: 50px;
    }
  }

  &__main {
    display: flex;
    flex: 0 0 494/1240*100%;
    align-items: center;

    @media (max-width: $md2+px) {
      flex: 1 1 auto;
    }
  }

  &__logo {
    font-weight: bold;
    font-size: 24px;
    line-height: 29/24*100%;
    color: #000;
    position: relative;
    z-index: 5;
  }

  &__search {
    @include adaptive-value("padding-right", 32, 15, 1);

    @media (min-width: $md2+px) {
      display: flex;
      flex: 1 1 auto;
    }
  }

}

.menu {
  flex: 0 1 420px;

  &__body {
    @media (max-width: $md3+px) {
      position: fixed;
      width: calc(100% - 30px);
      height: 100%;
      top: 0;
      left: -100%;
      overflow: auto;
      background-color: #fcf3f8;
      transition: all .3s ease 0s;
      padding: 70px 15px 30px 15px;

      &:before {
        content: '';
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 60px;
        background: #fcf3f8;
        transition: all .3s ease 0s;
        z-index: 6;
      }

      &.active {
        left: 0;

        &:before {
          left: 0;
        }
      }
    }
  }

  &__list {
    @media (min-width: $md3 + px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-evenly;
    }
  }

  &__arrow {
    display: flex;
    // justify-content: center;
    // align-items: center;
    font-size: 8px;
    margin-left: 8px;
    transition: all .3s ease 0s;

    &.active {
      transform: rotate(-180deg);
      color: $orange-color;
    }

    @media (max-width: $md3+px) {
      font-size: 14px;
      color: $main-color;
    }
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 3px;
    padding: 5px 0;
    position: relative;

    @media (any-hover: hover) {
      &:hover {
        .menu__sub-list {
          transform: translate(0, 0);
          opacity: 1;
          visibility: visible;
          pointer-events: auto;
        }

        .menu__arrow {
          transform: rotate(-180deg);
        }

        .menu__arrow,
        .menu__link {
          color: $orange-color;
        }
      }
    }

    @media (min-width: $md3+px) {
      &._hover {
        .menu__sub-list {
          transform: translate(0, 0);
          opacity: 1;
          visibility: visible;
          pointer-events: auto;
        }

        .menu__arrow {
          transform: rotate(-180deg);
        }

        .menu__arrow,
        .menu__link {
          color: $orange-color;
        }
      }
    }
  }

  &__link {
    font-weight: 500;
    color: inherit;
    transition: color .3s ease 0s;

    @media (max-width: $md3+px) {
      font-size: 24px;
    }
  }


  &__sub-list {
    position: relative;
    background: $orange-color;
    flex: 1 1 100%;
    padding: 15px;
    z-index: 5;

    @media (min-width: $md3+px) {
      opacity: 0;
      transition: all .3s ease 0s;
      transform: translate(0, 10px);
      visibility: hidden;
      min-width: 200px;
      top: 100%;
      left: 0;
      position: absolute;
      pointer-events: none;
    }

    @media (max-width: $md3+px) {
      margin: 0 -15px 0 -15px;
    }
  }

  &__sub-item {
    &:not(:last-child) {
      margin: 0 0 5px 0;

      @media (max-width: $md3+px) {
        margin: 0 0 10px 0;
      }
    }
  }

  &__sub-link {
    color: $white-color;
    line-height: 120%;

    &:hover {
      text-decoration: underline;
    }

    @media (max-width: $md3+px) {
      font-size: 18px;
    }
  }
}

.icon-menu {
  display: none;
  @media (max-width: $md3+px) {
      display: flex;
  position: relative;
  flex: 0 0 30px;
  height: 20px;
  @include adaptive-value("margin-left",32,15 , 1);
  &:before,
  &:after,
  span {
    content: '';
    background-color: $main-color;
    position: absolute;
    height: 2px;
    width: 100%;
    left: 0;
    transition: all 0.25s ease-in-out;
  }

  &:before {
    top: 0;
  }

  span {
    top: 9px;
    opacity: 1;
  }

  &:after {
    bottom: 0;
  }

  &.active {
    span {
      opacity: 0;
    }

    &:before {
      transform: rotate(45deg);
      top: 9px;
    }

    &:after {
      transform: rotate(-45deg) translateY(-50%);
      bottom: 9px;
    }
  }
  }

}

.search-form {
  max-width: 473px;
  width: 100%;

  &.active {
    .search-form__item {
      opacity: 1;
      visibility: visible;
      top: 100%;
    }

    .search-form__icon {
      color: $orange-color;
    }
  }

  &__icon {
    position: relative;
    z-index: 5;
    color: $icons-color;
    font-size: 22px;

    @media (min-width: $md2+px) {
      display: none;
    }
  }

  &__item {
    background-color: $white-color;
    display: flex;
    align-items: center;

    @media (max-width: $md2+px) {
      box-shadow: 0 0 5px rgba(212, 209, 205, .3);
      position: absolute;
      left: 0;
      width: 100%;
      top: -100%;
      z-index: 5;
      opacity: 0;
      visibility: hidden;
      transition: all .3s ease 0s;
    }
  }

  &__btn {
    display: flex;
    flex: 0 0 45px;
    height: 45px;
    color: #333;
    font-size: 15px;
    align-items: center;
    justify-content: center;
  }

  &__input {
    font-size: 14px;
    color: $dark-gray-color;
    width: 100%;
    padding-right: 20px;
  }
}

.actions-header {
  position: relative;
  z-index: 5;
  display: grid;
  grid-auto-flow: column;
  align-items: center;
  @include adaptive-value("gap", 32, 15, 1);

  &__item {

    &-favorite {
      color: $icons-color;
      font-size: 24px;
      transition: all .3s ease 0s;

      @media (any-hover: hover) {
        &:hover {
          color: $orange-color;
        }
      }
    }

    &_user {
      @include adaptive-value("width", 40, 30, 1);
      @include adaptive-value("height", 40, 30, 1);
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
    }
  }
}

.cart {

  // .cart-list
  &-list {
    overflow: auto;
    max-height: 200px;
  }

  // .cart-item
  &-item {
    display: flex;
    align-items: flex-start;

    &:not(:last-child) {
      margin: 0 0 15px 0;
    }

    // .cart-item__img

    &__img {
      display: flex;
      flex: 0 0 100px;
      height: 100px;
    }

    // .cart-item__body
    &__body {
      flex:1 1 auto;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding-left: 10px;
    }

    // .cart-item__title
    &__title {
      color: inherit;
      font-weight: 600;
      transition: all .3s ease 0s;
      margin-bottom: 5px/16px*1em;
      @media (any-hover: hover) {
        &:hover{
          color: $orange-color;
        }
      }
    }

    // .cart-item__qty
    &__qty {
      font-size: 14px;
      margin-bottom: 5px/14px*1em;
    }

    // .cart-item__delete
    &__delete {
      // background: rgb(180, 14, 14);
      color: rgb(180, 14, 14);
      padding: 3px 5px;
      cursor: pointer;
      font-weight: 600;
    }
  }
}

.cart-header {
  &__icon {
    color: $icons-color;
    font-size: 24px;
    transition: all .3s ease 0s;
    position: relative;
    display: block;

    @media (any-hover: hover) {
      &:hover {
        color: $orange-color;
      }
    }

    span {
      position: absolute;
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      top: -10px;
      right: -10px;
      background: $orange-color;
      border-radius: 50%;
      color: $white-color;
      font-size: 12px;
      font-family: inherit;
    }
  }
  &__body{
    top: 60px;
    position: absolute;
    border: 1px solid #e0e0e0;
    background: #f4f5f7;
    right: 0;
    width: 300px;
    padding: 15px;
    z-index: 5;
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease 0s;
    transform: translate(0, -20px);
    .active &{
      opacity: 1;
      visibility: visible;
      transform: translate(0, 0);
    }
  }
}